}

.content ul,
.content ol {
  margin: 0 0 25px;
  padding: 0;
  list-style-type: none;
}

.content ul li,
.content ol li {
  padding-left: 40px;
  margin-bottom: 10px;
  position: relative;
}

.content ul li::before {
  content: ' ';
  position: absolute;
  width: 24px;
  height: 3px;
  top: 12px;
  left: 0;
  border-radius: 1px;
  background-color: #f9314b;
}

.content ol {
  counter-reset: list;
}

.content ol li::before {
  content: ' ';
  position: absolute;
  counter-increment: list;
  content: counter(list);
  width: 24px;
  height: 24px;
  top: 1px;
  left: 0;
  color: #fff;
  font-size: 16px;
  line-height: 24px;
