.pagination {
  padding: 20px 0;
  display: block;
}

.pagination ul {
  padding: 0;
  margin: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  list-style-type: none;
}

.pagination li {
  display: inline-block;
  height: 31px;
  text-align: center;
}

.pagination a,
.pagination span {
  display: block;
  width: 31px;
  height: 100%;
  color: #b7bed8;
  font-size: 22px;
  font-weight: 700;
  border-radius: 4px;
  text-decoration: none;
  margin: 0 1px;
  position: relative;
}

.pagination a:not(.prev), .pagination a:not(.next),
.pagination span:not(.prev),
.pagination span:not(.next) {
  display: inline-block;
  font-size: 18px;
  line-height: 31px;
}

@media only screen and (max-width: 550px) {
  .pagination a:not(.prev), .pagination a:not(.next),
  .pagination span:not(.prev),
  .pagination span:not(.next) {
    width: 30px;
    font-size: 24px;
  }
}

@media only screen and (max-width: 430px) {
  .pagination a:not(.prev), .pagination a:not(.next),
  .pagination span:not(.prev),
  .pagination span:not(.next) {
    width: 25px;
    font-size: 22px;
  }
}

.pagination a:hover {
  color: #fff;
  background-color: #f9314b;
  text-decoration: none;
}

.pagination a.prev::before, .pagination a.prev::after, .pagination a.next::before, .pagination a.next::after {
  position: absolute;
  content: '';
  width: 10px;
  height: 3px;
  left: 50%;
  background-color: #b7bed8;
}

.pagination a.prev::before, .pagination a.next::before {
  top: 15px;
}

.pagination a.prev::after, .pagination a.next::after {
  top: 10px;
}

.pagination a.prev:hover, .pagination a.next:hover {
  color: #fff;
  background-color: #f9314b;
  border-bottom: 0;
  box-shadow: none;
}

.pagination a.prev:hover::before, .pagination a.prev:hover::after, .pagination a.next:hover::before, .pagination a.next:hover::after {
  background-color: #fff;
}

.pagination a.prev::before {
  transform: translateX(-50%) rotate(45deg);
}

.pagination a.prev::after {
  transform: translateX(-50%) rotate(-45deg);
}

.pagination a.next::before {
  transform: translateX(-50%) rotate(-45deg);
}

.pagination a.next::after {
  transform: translateX(-50%) rotate(45deg);
}

.pagination a.prev span,
.pagination a.next span {
  display: none;
}

.pagination .current {
  color: #fff;
  text-decoration: none;
  font-weight: 700;
  background-color: #f9314b;
}

