  .footer-menu .menu li {
    margin: 5px 0;
  }
}

.header-menu .menu li a,
.footer-menu .menu li a {
  padding: 30px 0;
  display: inline-block;
  color: #b7bed8;
  font-weight: 500;
  font-size: 20px;
  line-height: 60px;
  position: relative;
}

@media only screen and (max-width: 991px) {
  .header-menu .menu li a,
  .footer-menu .menu li a {
    padding: 5px 0;
    line-height: 20px;
  }
}

.header-menu .menu li a::before,
.footer-menu .menu li a::before {
  content: ' ';
  position: absolute;
  top: 0;
  left: calc(50% - 10px);
  opacity: 0;
  transition: all 0.3s ease-out;
  width: 0px;
  height: 0px;
  border-style: solid;
  border-width: 10px 10px 0 10px;
  border-color: #f9314b transparent transparent transparent;
}

@media only screen and (max-width: 991px) {
  .header-menu .menu li a::before,
  .footer-menu .menu li a::before {
    top: calc(50% - 10px);
    left: -20px;
    width: 0px;
    height: 0px;
    border-style: solid;
    border-width: 10px 0 10px 10px;
    border-color: transparent transparent transparent #f9314b;
  }
}

.header-menu .menu li a::after,
.footer-menu .menu li a::after {
  content: ' ';
  position: absolute;
  bottom: 0;
  left: calc(50% - 10px);
  opacity: 0;
  transition: all 0.3s ease-out;
  width: 0px;
  height: 0px;
  border-style: solid;
  border-width: 0 10px 10px 10px;
  border-color: transparent transparent #f9314b transparent;
}

@media only screen and (max-width: 991px) {
  .header-menu .menu li a::after,
  .footer-menu .menu li a::after {
    top: calc(50% - 10px);
    left: auto;
    right: -20px;
    width: 0px;
    height: 0px;
    border-style: solid;
    border-width: 10px 10px 10px 0;
    border-color: transparent #f9314b transparent transparent;
  }
}

.header-menu .menu li a:hover::before, .header-menu .menu li a:hover::after,
.footer-menu .menu li a:hover::before,
.footer-menu .menu li a:hover::after {
  opacity: 1;
}

.header-menu .menu li.current-menu-item a::before, .header-menu .menu li.current-menu-item a::after,
.footer-menu .menu li.current-menu-item a::before,
.footer-menu .menu li.current-menu-item a::after {
  opacity: 1;
}

