.btn {
	min-width: 145px;
	display: inline-block;
	padding: 10px 40px;
	font-size: 18px;
	line-height: 20px;
	text-transform: uppercase;
	text-align: center;
	text-decoration: none;
	border: 0;
	border-radius: 10px;
	box-shadow: 3px 3px 3px rgba(0, 0, 0, 0.2);
	transition: $transition;
	cursor: pointer;
	@include r(1259) {
		font-size: 16px;
		padding: 10px 25px; } }

.btn-accent {
	color: $color-white;
	background-color: $color-accent;

	&:hover,
	&:focus,
	&:active {
		color: $color-white;
		background-color: lighten($color-accent, 15%); } }

.btn-light {
	color: $color-dark;
	background-color: $color-blue;

	&:hover,
	&:focus,
	&:active {
		color: $color-dark;
		background-color: darken($color-blue, 20%); } }

.btn-big {
	padding: 15px 42px;
	@include r(1259) {
		padding: 15px 30px; } }

.btn-fullwidth {
	width: 100%;
	text-align: center; }

