.container {
	width: 100%;
	padding-right: 15px;
	padding-left: 15px;
	margin-right: auto;
	margin-left: auto;
	@include rmin(1260) {
		max-width: 1200px; }
	@include rmin(992) {
		max-width: 960px; }
	@include rmin(768) {
		max-width: 720px; }
	@include rmin(576) {
		max-width: 540px; } }

.row {
	display: flex;
	flex-wrap: wrap; }

.content {

	img {
		height: auto; }

	ul,
	ol {
		margin: 0 0 25px;
		padding: 0;
		list-style-type: none;

		li {
			padding-left: 40px;
			margin-bottom: 10px;
			position: relative; } }

	ul {

		li {
			@include pseudo(before) {
				width: 24px;
				height: 3px;
				top: 12px;
				left: 0;
				border-radius: 1px;
				background-color: $color-accent; } } }

	ol {
		counter-reset: list;

		li {
			@include pseudo(before) {
				counter-increment: list;
				content: counter(list);
				width: 24px;
				height: 24px;
				top: 1px;
				left: 0;
				color: #fff;
				font-size: 16px;
				line-height: 24px;
				text-align: center;
				background-color: $color-accent;
				border-radius: 0 50% 50%; } } } }

.section {
	margin-bottom: 60px; }

.main {
	background-color: $color-block-bg;
	border-radius: $border-radius;
	padding: 30px;
	margin-bottom: 60px; }

.screens-box {
	margin: 0 -30px 30px;
	padding: 30px;
	background-color: $color-dark;

	.swiper-pagination-bullets {
		padding-top: 15px;
		display: flex;
		justify-content: center; }

	.swiper-pagination-bullet {
		background-color: $color-blue;
		opacity: 1;

		&.swiper-pagination-bullet-active {
			background-color: $color-accent; } } }

